div.categories-container {
    display: flex;
    overflow-x: auto;
    height: 5rem;
    scroll-snap-type: x mandatory;
    position: sticky;
    z-index: 50;
    top:0px;
    background-color: white;
    box-shadow: 0 2px 4px 0 rgba(0,0,0,.1)
}

.category-tag{
    scroll-snap-align: center;
    scroll-snap-stop: always;
    display: inline-block;
    text-align: start;
    flex-shrink: 0;
    font-size: 1.5rem;
    height: 3rem;
    padding: 1.4rem .5rem;
    margin: 0 1rem;
    color: #a5a5aa;
}

.category-tag-active{
    position: relative;
    font-weight: bold;
    color: #444;
}

.category-tag-active::after{
    position: absolute;
    top: 0;
    left: 0;
    content: '';
    height: 4.6rem;
    width: 100%;
    border-bottom: #deb566 solid 3px;
    animation: border-grow .3s cubic-bezier(0.4,0,0.4,1) 0s;
}

@keyframes border-grow {
    0% {
        opacity: 0;
        width: 0
    }

    to {
        opacity: 1;
        width: 100%
    }
}
