div.menu-container{
    background-color: #e8e8e8;
}

.card-mask {
    z-index: 200;
    position: fixed;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    background-color: rgba(0, 0, 0, 0.6);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.card-mask .close button {
    margin-top: 1rem;
    font-size: 2rem;
    color: white;
    width: 4rem;
    height: 4rem;
    border-radius: 50%;
    background-color: rgba(0, 0, 0, 0.6);
    border: none;
}

