
.card-detail{
    width: 92vw;
    background: #fff;
    border-radius: 3px;
    flex-direction: column;
    display: flex;
    justify-content: space-between;
    box-shadow: 0 1px 3px rgba(255,255,255,.1);
    max-height: 80%;
}

.card-detail .product-title{
    padding: 1rem;
    text-align: center;
    font-weight:bold;
    font-size: 1.15rem;
    border-bottom: 1px solid #f0f2f7;
    display: block;
    width: 100%;
}

.card-detail .product-img{
    border-radius: 4px;
    object-fit: contain;
}

.card-detail .description-info{
    padding: 2px 1rem;
}

.card-detail .allergen-info{
    padding: 2px 1rem;
}

.card-detail .description-info h2{
    font-size: 4vw;
    font-weight: bold;
    color: #07111b;
}

.card-detail .description-info p{
    margin-top: 1.5vw;
    font-size: 3.5vw;
    line-height: 4.5vw;
    color: #93999f;
}

.card-detail .allergen-info h2{
    font-size: 4vw;
    font-weight: bold;
    color: #07111b;
}

.card-detail .allergen-info p{
    margin-top: 1.5vw;
    font-size: 3.5vw;
    line-height: 4.5vw;
    color: #93999f;
}



