.shop-cart {
    position: fixed;
    left: 0;
    bottom: 0;
    z-index: 50;
    width: 100%;
    height: 48px;
}
.shop-cart .content {
    display: flex;
    background: #141d27;
    font-size: 0;
    color: rgba(255, 255, 255, 0.4);
}
.shop-cart .content .content-left {
    flex: 1;
}
.shop-cart .content .content-left .logo-wrapper {
    display: inline-block;
    vertical-align: top;
    position: relative;
    top: -10px;
    margin: 0 12px;
    padding: 6px;
    width: 56px;
    height: 56px;
    box-sizing: border-box;
    border-radius: 50%;
    background: #141d27;
}
.shop-cart .content .content-left .logo-wrapper .logo {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    text-align: center;
    background: #2b343c;
}
.shop-cart .content .content-left .logo-wrapper .logo.highlight {
    background: #fdbc24;
}
.shop-cart .content .content-left .logo-wrapper .logo .icon-shopping_cart {
    line-height: 44px;
    font-size: 24px;
    color: #80858a;
}
.shop-cart .content .content-left .logo-wrapper .logo .icon-shopping_cart.highlight {
    color: #fff;
}
.shop-cart .content .content-left .logo-wrapper .num {
    position: absolute;
    top: 0;
    right: 0;
    width: 28px;
    height: 18px;
    line-height: 18px;
    text-align: center;
    border-radius: 16px;
    font-size: 8px;
    font-weight: 700;
    color: #fff;
    background: #f01414;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.4);
}


.shop-cart .content .content-left .price {
    display: inline-block;
    vertical-align: top;
    margin-top: 12px;
    line-height: 24px;
    padding-right: 12px;
    box-sizing: border-box;
    border-right: 1px solid rgba(255, 255, 255, 0.1);
    font-size: 16px;
    font-weight: 700;
}

.shop-cart .content .content-left .price.highlight {
    color: #fff;
}

.shop-cart .content .content-right {
    flex: 0 0 105px;
    width: 105px;
}
.shop-cart .content .content-right .pay {
    height: 48px;
    line-height: 48px;
    text-align: center;
    font-size: 12px;
    font-weight: 700;
}

.shop-cart .content .content-right .pay.not-enough {
    background: #2b333b;
}
.shop-cart .content .content-right .pay.enough {
    background-color: #fdbc24;
    color: #fff;
}


.shop-cart .ball {
    position: fixed;
    z-index: 200;
}
.shop-cart .ball .inner {
    width: 16px;
    height: 16px;
    background: #fdbc24;
    border-radius: 50%;
}
