
div.food-wrapper {
    margin: 0.5rem 0rem;
    display: flex;
    flex-wrap: nowrap;
    position: relative;
    height: 14rem;
    padding: 1.2rem ;
    background-color: white;
    /*border-radius: 2px;*/
    /*-webkit-box-shadow: 0 1px 3px rgba(18,18,18,.1);*/
    /*box-shadow: 0 1px 3px rgba(18,18,18,.1);*/
}

.food-wrapper img.product-img {
    display: block;
    margin: 6px;
    width: 96px;
    height: 96px;
    object-fit: contain;
    align-self: center;
}

.food-wrapper .product-info {
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    margin: 6px;
}

.food-wrapper .product-name {
    font-size: 1.5rem;
    color: #434344;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 1; /* number of lines to show */
    -webkit-box-orient: vertical;
}

.food-wrapper .product-description {
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2; /* number of lines to show */
    -webkit-box-orient: vertical;

}

.food-wrapper .allergen-info ul {
    display: flex;

}

li img {
    display: block;
    width: 16px;
    margin-right: 2px;
}

div.price-wrapper {
    margin-top: auto;
}

.cart-control-container {
    position: absolute;
    right: 8px;
    bottom: 16px;
}
