
header{
    height: 20vh;
    min-height: 160px;
    background: url(https://cdn.shortpixel.ai/client/to_webp,q_glossy,ret_img,w_1920/https://hoteljaizkibel.com/wp-content/uploads/2018/09/restaurante-jaizkibel-1.jpg) center;
    background-size: cover;
    position: relative;
}

div.header-wrapper{
    text-align: center;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    background-color: rgba(0,0,0,.5);
}



.header-wrapper h1.title{
    font-size: 3rem;
    color: white;

}

.header-wrapper p.subtitle{
    color: white;
    font-size:1.5rem;
    line-height: 3rem;
}


