.cart-control {
    font-size: 0;
}

.cart-control .cart-decrease {
    display: inline-block;
    padding: 6px;
    opacity: 0;
    visibility: hidden;
    transform: translate(24px, 0);
    transition: all 0.4s;
}

.cart-control .cart-decrease .inner {
    display: inline-block;
    line-height: 28px;
    font-size: 28px;
    color: #fdbc24;
    transition: all 0.4s linear;
    transform: rotate(180deg);
}

.cart-control .cart-decrease.move {
    transform: translate(0, 0);
    opacity: 1;
    visibility: visible;
    cursor: pointer;
}

.cart-control .cart-decrease.move .inner {
    transform: rotate(0);
}

.cart-control .cart-count {
    display: inline-block;
    vertical-align: top;
    width: 30px;
    padding-top: 8px;
    line-height: 24px;
    text-align: center;
    font-size: 16px;
    color: #636568;
}

.cart-control .cart-add {
    display: inline-block;
    padding: 6px;
    line-height: 28px;
    font-size: 28px;
    color: #fdbc24;
    cursor: pointer;
}


.cart-control .choose {
    background: #fdbc24;
    color: #fff;
    font-weight: bold;
    font-size: 12px;
    padding: 4px 6px;
    border-radius: 3px;
    position: relative;
    display: inline-block;
}

.cart-control .choose-num {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 20px;
    height: 20px;
    padding: 5px;
    right: 0px;
    top: -10px;
    font-size: 12px;
    line-height: 12px;
    color: #fff;
    background: #e0343f;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.15);
    border-radius: 50%;
    transform: scale(0.8);
    transform-origin: center;
}

.specification-card-container {
    position: fixed;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    background-color: rgba(255,255,255,0.5);
    z-index: 100;
}

